import { useContext } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { MainContext } from "../../../contexts/mainProvider";

export const Header = () => {
  const { logout, sidebar, setSidebar, user } = useContext(MainContext)
  const navigate = useNavigate();


  const logOut = () => {
    logout()
    navigate('/login')
  }

  return (
    <div className="header-section">
      <a onClick={() => setSidebar(!sidebar)} className="toggle-btn"><img className="navigicon" src={`assets/apps/images/navig.png`} alt="" /></a>
      <div className="menu-right">
        <ul className="top-right-menu">
          <li>
            <a href="#" className="btn btn-default dropdown-toggle" data-toggle="dropdown">{user?.name} <span className="caret"></span></a>
            <ul className="dropdown-menu dropdown-menu-usermenu pull-right">
              <li><NavLink to="/profile" ><i className="fa fa-user"></i> Profile</NavLink></li>
              <li style={{ cursor: "pointer" }}><a onClick={() => logOut()}><i className="fa fa-sign-out"></i> Log Out</a></li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  );
};
