import { useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { MainContext } from "../../contexts/mainProvider";
import { Formik } from "formik";
import * as Yup from "yup";
import { usePost } from "../../contexts/usePost";
import { InputBox, PasswordBox, Buttons } from "../../components/RenderFroms";
const resource = "system_users";

export default function Login() {
  let navigate = useNavigate();
  const { labels, login, isLoading, token } = useContext(MainContext)
  const { create, data: respond, loading } = usePost();

  useEffect(() => {
    if (token) {
      navigate('/', { replace: true })
    }
  }, [token])

  useEffect(() => {
    if (respond) {
      console.log(respond)
    }
  }, [respond])

  const validationSchema = Yup.object().shape({
    email: Yup.string().required("Please enter your login username. e.g. user@example.com."),
    password: Yup.string()
      .min(5, "Password must be at least 6 characters")
      .required("Please enter your login password."),
  });

  const onPressLogin = async (values: any) => {
    login({ ...values })
  };

  const validationSchema1 = Yup.object().shape({
    email: Yup.string().required("Please enter your login username. e.g. user@example.com."),
  });

  const onPressResetPwd = async (values: any) => {
    create(`${resource}/send-password-reset-email/${values.email}`, values)
  };

  return (
    <>
      <div className="row login">
        <div className="card whitebg">
          <div className="card-header bg5">
            <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <header className="text-lg text-normal">User Login for Instrowest Apps</header>
            </div>
            <div className="clear"></div>
          </div>
          <div className="card-body">
            <div className="row no-margin note">
              Enter your appropriate login credentials into the "Username" and "Password" fields respectively, then click "Login".
            </div>
            <div className="form floating-label has-validation-callback">
              <Formik
                initialValues={{ email: "apps@instrowest.com.au", password: "admin12345" }}
                validationSchema={validationSchema}
                onSubmit={(values) => onPressLogin(values)}
              >
                {({ handleChange, handleBlur, handleSubmit, values, errors }) => (
                  <>
                    <div className="card-body">
                      <InputBox
                        required={true}
                        name="email"
                        template="floating"
                        lable="Your Login Username"
                      />
                      <PasswordBox
                        required={true}
                        name="password"
                        template="floating"
                        lable="Your Login Password"
                      />
                    </div>
                    <div className="card-action">
                      <div className="text-right">
                        <Buttons className="btn btn-success btn-raised text-uppercase white ripple" value={labels.logIn} loading={isLoading} onClick={handleSubmit} />
                      </div>
                    </div>
                  </>
                )}
              </Formik>
            </div>
          </div>
        </div>
      </div>

      <div className="row resetpwd">
        <div className="card whitebg">
          <div className="card-header bg5">
            <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <header className="text-lg text-normal">Reset Password</header>
            </div>
            <div className="clear"></div>
          </div>
          <div className="card-body">
            <div className="row no-margin note">
              To obtain your login credentials, please enter valid username. Your new credentials will be e-mailed to you.
            </div>
            <div className="form floating-label has-validation-callback">
              <Formik
                initialValues={{ email: "apps@instrowest.com.au" }}
                validationSchema={validationSchema1}
                onSubmit={(values) => onPressResetPwd(values)}
              >
                {({ handleChange, handleBlur, handleSubmit, values, errors }) => (
                  <>
                    <div className="card-body">
                      <InputBox
                        required={true}
                        name="email"
                        template="floating"
                        lable="Your Login Username"
                      />
                    </div>
                    <div className="card-action">
                      <div className="text-right">
                        <Buttons className="btn btn-success btn-raised text-uppercase white ripple" value={"Reset Password"} loading={isLoading} onClick={handleSubmit} />
                      </div>
                    </div>
                  </>
                )}
              </Formik>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}