import axios from "axios";
import { toast } from 'react-toastify';

export const axiosInstance: any = axios.create({
    baseURL: `https://api-inst.erptech.in/`,
    // baseURL: `http://localhost:5001/`,
    // baseURL: `${process.env.API_URL}/`,
    headers: {
        "Accept": "application/json"
    },
});

const showError = (error: any) => {
    if (error?.response?.data?.message || error?.response?.data?.error) {
        return toast.error(error?.response?.data?.message || error?.response?.data?.error);
    }
    let messages = error?.response?.data?._server_messages ? JSON.parse(JSON.parse(error?.response?.data?._server_messages)[0]) : null
    if (messages) {
        return toast.error(messages?.message);
    }
    if (error?.code == "ERR_NETWORK" || error?.code == "ERR_BAD_RESPONSE") {
        // window.location.href = "/login";
    }
};

const SiteApis = {
    login: async (body: any) => {
        return axiosInstance
            .post("system_users/sign-in", body, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response?.data
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
    logout: async () => {
        return true
    },
    getDataApi: async (params: any, token: any) => {
        axiosInstance.defaults.headers['Authorization'] = token
        let URL = params.url + "?"
        for (let key in params.query) {
            URL += `${key}=${params.query[key]}&`
        }
        return axiosInstance
            .get(URL, params.query, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response?.data
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
    getSingleDataApi: async (params: any, token: any) => {
        axiosInstance.defaults.headers['Authorization'] = token
        let URL = params.url + "/" + params.id
        return axiosInstance
            .get(URL, {}, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response?.data
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
    addDataApi: async (params: any, token: any) => {
        axiosInstance.defaults.headers['Authorization'] = token
        return axiosInstance
            .post(params.url, params.body, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
    editDataApi: async (params: any, token: any) => {
        axiosInstance.defaults.headers['Authorization'] = token
        return axiosInstance
            .patch(params.url + '/' + params?.body?.id, params.body, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
    deleteDataApi: async (params: any, token: any) => {
        axiosInstance.defaults.headers['Authorization'] = token
        const URL = `${params.url}/${params.body.id}`
        return axiosInstance
            .delete(URL, params.body, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response.data
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
    fileUploadApi: async (url: any, file: any, token: any) => {
        let body = new FormData();
        body.append("file", file, file.name);
        return axiosInstance
            .post(url, body, { withCredentials: false })
            .then((response: any) => {
                if (response?.status == 200 || response?.status == 201 || response?.status == 202) {
                    return response?.data?.data
                } else {
                    throw new Error(response)
                }
            })
            .catch((error: any) => {
                showError(error);
                return { error: true, message: error?.message }
            });
    },
};

export default SiteApis;