import React, { useContext, useState, useRef } from "react";
import { Field } from "formik";
import { Input, Button, DatePicker, Select, TreeSelect, Checkbox, Switch } from "antd";
import apis from "../contexts/apis";
import { IoIosArrowDown } from "react-icons/io";
import { MainContext } from "../contexts/mainProvider";

const { SHOW_PARENT } = TreeSelect;
const { TextArea } = Input;

const buildTree = (items: any, parentKey = null) => {
  const map: any = {};
  const roots: any = [];

  // Create a mapping of id to item
  items.forEach((item: any, index: any) => {
    item['title'] = item.name
    item['value'] = item.id
    item['key'] = parentKey ? `${parentKey}-${index}` : `0-${index}`;
    map[item.id] = { ...item, children: [] };
  });


  items.forEach((item: any, key: any) => {
    if (item.parentId && map[item.parentId]) {
      map[item.parentId].children.push(map[item.id]);
    } else {
      roots.push({ ...map[item.id], key: `0-${key}` });
    }
  });

  // console.log(roots)
  return roots;
}

const ButtonBox = (props: any) => {
  return (
    <Button type="primary" {...props} block size="large">
      {props.loading ? "Processing..." : props.value}
      {props.icons}
    </Button>
  );
};

const Buttons = (props: any) => {
  return (
    <button {...props} size="large" className={`btn btn-raised text-uppercase ripple mr-10 ${props.className ?? ''}`}>
      {props.loading ? "Processing..." : props.value}
    </button>
  );
};

const InputBox = (props: any) => {
  return (
    <Field name={props.name}>
      {({ field, form, meta }: any) => {
        if (props.template === "floating") {
          return (
            <div className={form.errors[props.name] ? "form-group has-error" : "form-group has-success"}>
              <Input
                value={field.value}
                onChange={(obj) => { form.setFieldValue(props.name, obj.target.value) }}
                prefix={props.icon}
                className={`form-control no-outline login valid ${meta?.error && ("error")}`}
                {...props}
              />
              <label className="text-normal">{props.lable}</label>
              {meta?.error && (<span className="help-block form-error">{form.errors[props.name]}</span>)}
            </div>
          )
        }
        return (
          <div className="form-group">
            {props.lable && (<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">{props.lable}{props.required && (<span className="danger">*</span>)}</label>)}
            <div className={`col-lg-6 col-md-6 col-sm-12 col-xs-12 ${meta?.error && ("has-error")}`}>
              <Input
                value={field.value}
                onChange={(obj) => { form.setFieldValue(props.name, obj.target.value) }}
                prefix={props.icon}
                className={`form-control ${meta?.error && ("error")}`}
                {...props}
              />
              {meta?.error && (<span className="help-block form-error">{form.errors[props.name]}</span>)}
            </div>
          </div>
        );
      }}
    </Field>

  );
};

