const SelectBox = (props: any) => {
  return (
    <Field name={props.name}>
      {({ field, form, meta }: any) => {
        return <>
          <div className="form-group">
            <label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">{props.lable}{props.required && (<span className="danger">*</span>)}</label>
            <div className={`col-lg-6 col-md-6 col-sm-12 col-xs-12 checkBoxStyle ${meta?.error && ("has-error")}`}>
              <Select
                mode={props.mode ?? false}
                allowClear
                className={`form-control ${meta?.error && ("error")}`}
                placeholder={props.placeholder ?? "Please select"}
                defaultValue={field.value}
                showSearch
                onChange={(obj: any) => {
                  form.setFieldValue(props.name, obj);
                }}
                filterOption={(input: any, option: any) => {
                  return option.label.toLowerCase().includes(input.toLowerCase())
                }
                }
                options={props.options}
                {...props}
              />
              {meta?.error && (<span className="help-block form-error">{form.errors[props.name]}</span>)}
            </div>
          </div>
        </>
      }}
    </Field>
  );
};

