import { useContext, useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { LiaProductHunt } from "react-icons/lia";
import { MainContext } from "../../../contexts/mainProvider";

const menus: any = [
  {
    icon: "apps.png",
    label: " Jump To",
    children: [
      {
        label: "OMS Dashboard",
        value: "orders",
      },
    ]
  },
  {
    icon: "home.png",
    label: <span>Main Dashboard</span>,
    value: "/",
  },
  {
    icon: "products.png",
    label: <span>Products</span>,
    value: "products",
  },

];

export const Sidebar = () => {
  const { logout, user } = useContext(MainContext)
  const navigate = useNavigate();
  const [activeMenu, setActiveMenu] = useState<any>({})

  const logOut = () => {
    logout()
    navigate('/login')
  }


  return (
    <div className="left-side sticky-left-side">
      <div className="logo"><NavLink to="/" ><img src="assets/apps/images/logo.jpg" alt="" /></NavLink></div>
      <div className="logo-icon text-center"><NavLink to="/" ><img src="assets/apps/images/logo_icon.jpg" alt="" /></NavLink> </div>
      <div className="left-side-inner">
        <div className="visible-xs hidden-sm hidden-md hidden-lg">
          <div className="media logged-user">
            <div className="media-body">
              <h4><a href="#">{user?.firstName}</a></h4>
              <span>"Hello There..."</span>
            </div>
          </div>
          <h5 className="left-nav-title">Account Information</h5>
          <ul className="nav nav-pills nav-stacked custom-nav">
            <li><NavLink to="/profile" ><i className="fa fa-user"></i> Profile</NavLink></li>
            <li style={{ cursor: "pointer" }}><a onClick={() => logOut()}><i className="fa fa-sign-out"></i> Sign Out</a></li>
          </ul>
        </div>

        <ul className="nav nav-pills nav-stacked custom-nav">

          {menus.map((item: any, key: any) => <li key={key} className={activeMenu[key] ? "menu-list nav-active" : "menu-list"}>
            {item.value ? <NavLink to={item.value}><img className="navigicon" src={`assets/apps/images/${item.icon}`} alt="" /> {item.label}</NavLink> :
              <a onClick={() => setActiveMenu({ [key]: true })}><img className="navigicon" src={`assets/apps/images/${item.icon}`} alt="" /> <span>{item.label}</span></a>}
            {item.children && (<ul className="sub-menu-list">
              {item.children.map((name: any, k: any) => <li key={k}><NavLink to={name.value}>{name.label}</NavLink></li>)}
            </ul>)}
          </li>)}

          <li style={{ cursor: "pointer" }}><a onClick={() => logOut()}><img className="navigicon" src={`assets/apps/images/logout.png`} alt="" /> <span>Logout</span></a></li>
        </ul>

      </div>
    </div>
  );
};